@mixin initial_day() {
  @each $day in $days-list {
    $i: index($days-list, $day);
    .calendar-#{$day} div:first-child {
      grid-column-start: #{$i};
    }
  }
}

@mixin amount_of_days() {
  @for $i from 28 through 30 {
    .calendar-#{$i} div:nth-child(#{$i}) ~ div {
      display: none;
    }
  }
}
